



* {
    font-family: 'Roboto', sans-serif; }


header {
    position: relative; }

nav {
    height: 50px;
    background-color: #212121; }

.menu {
    display: flex;
    justify-content: space-between;
    padding: 13px 70px;
    margin: 0;
    list-style-type: none;
    .menu-link {
        color: #ffffff;
        font-family: Roboto;
        font-size: 15px;
        font-weight: 300; }
    .menu-item {
        border-right: 2px dotted black;
        padding-right: 28px; } }

.subheder {
    position: absolute;
    width: 100%;
    height: 84px;
    background-color: rgba(0,0,0, .6);
    .subheader-logo {
        display: block;
        max-width: 100%;
        margin-top: 16px; }
    .subheader-official {
        opacity: 0.5;
        color: #ffffff;
        font-size: 13px;
        font-weight: 400;
        margin-top: 6px; }
    .subheader-coll {
        color: #ffffff;
        font-size: 12px;
        font-weight: 300;
        text-align: right;
        margin-top: 19px; }
    .subheader-phone {
        margin-top: 8px;
        color: #ffffff;
        font-size: 22px;
        font-weight: 400;
        text-align: right;
        text-transform: uppercase;
        line-height: 12px; }
    .subheader-btn {
        margin-top: 19px;
        width: 172px;
        height: 46px;
        box-shadow: inset 0 -3px 0 rgba(0, 0, 0, 0.12);
        border: none;
        border-radius: 4px;
        background-image: linear-gradient(to top, #1eacc7 0%, #4ce2ff 100%);
        text-shadow: 1px 1px 0 rgba(0, 0, 0, 0.21);
        color: #ffffff;
        font-size: 14px;
        font-weight: 400;
        text-transform: uppercase; } }

.promo {
    min-height: 800px;
    background: url(../img/promo-background.png) center center/cover no-repeat;
    padding: 150px 0 135px 0;
    transform: translateY(-84);
    .promo-header {
        color: #ffffff;
        font-size: 60px;
        font-weight: 700;
        text-align: center;
        text-transform: uppercase;
        line-height: 60px;
        margin: 0; }
    .promo-subheader {
        color: #ffffff;
        font-size: 36px;
        font-weight: 700;
        text-align: center;
        text-transform: uppercase;
        line-height: 36px;
        margin: 0;
        margin-top: 22px; }
    .promo-descr {
        color: #ffffff;
        font-size: 18px;
        font-weight: 400;
        line-height: 24px;
        text-align: center;
        margin-top: 23px; }
    .promo-btn {
        display: block;
        margin: 150px auto 0 auto;
        width: 259px;
        height: 63px;
        box-shadow: inset 0 -4px 0 rgba(0, 0, 0, 0.12);
        border: none;
        border-radius: 4px;
        background-image: linear-gradient(to top, #1eacc7 0%, #4ce2ff 100%);
        text-shadow: 1px 1px 0 rgba(0, 0, 0, 0.21);
        color: #ffffff;
        font-size: 18px;
        font-weight: 400;
        text-align: center;
        text-transform: uppercase; } }

.advantages {
    min-height: 764px;
    padding: 71px 0 65px 0;
    .advantages-btn {
        width: 112px;
        height: 24px;
        margin: 0 auto;
        background-color: #000000;
        color: #ffffff;
        font-size: 11px;
        font-weight: 400;
        text-align: center;
        vertical-align: middle;
        text-transform: uppercase; }
    .advantages-header {
        color: #222222;
        font-size: 38px;
        font-weight: 700;
        line-height: 48px;
        text-align: center;
        text-transform: uppercase;
        margin-top: 38px; }
    .icon-background {
        display: flex;
        justify-content: center;
        align-items: center;
        margin: 0 auto;
        width: 113px;
        height: 113px;
        background-color: #1eacc7;
        border-radius: 100%; }
    .advantages-descr {
        background-color: #fff;
        padding: 0;
        .desc-header {
            color: #1eacc7;
            font-size: 17px;
            font-weight: 400;
            margin: 0; }
        .desc-text {
            color:#202020 {}
            font-size: 15px;
            font-weight: 300;
            text-align: justify; } } }

